.Projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;

  &_Section {
    scroll-margin-top: 100px;

    h3 {
      transition: all 0.5s;
      -webkit-text-stroke: 4px #d6f4f4;
      font-variation-settings: "wght" 900, "ital" 1;
      font-size: 100px;
      text-align: center;
      color: transparent;
      font-family: "Meta", sans-serif;
      text-shadow: 5px 5px 0px #07bccc,
      10px 10px 0px #e601c0,
      15px 15px 0px #e9019a,
      20px 20px 0px #f40468,
      30px 30px 5px #482896;
      cursor: pointer;
      margin-bottom: 50px;

      &:hover {
        text-shadow: none;
        color: #ffffff;
        -webkit-text-stroke: 0px #d6f4f4;
      }
    }

    &_List {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 1200px;
      justify-content: center;
      align-items: center;
    }
  }
}

@media only screen and (max-width: 390px) {
  .Projects {
    &_Section {
      h3 {
        font-size: 65px;
      }
    }
  }
}